import type React from "react"
import type { Metadata } from "next"
import { Inter } from "next/font/google"
import "../globals.css"

const inter = Inter({ subsets: ["latin"] })

export const metadata: Metadata = {
  title: "KolamProsper dApp",
  description: "Tokenized T-bonds and property deeds with secure staking and non-liquidating loan options",
}

export default function DAppLayout({
  children,
}: {
  children: React.ReactNode
}) {
  return (
    <html lang="en" suppressHydrationWarning>
      <body className={inter.className}>
        <div className="min-h-screen bg-gradient-to-b from-black to-gray-900 text-white">{children}</div>
      </body>
    </html>
  )
}

